    })
  
    
  },
  methods: {
    redirect_on_start(){
      if(window.location.href.includes("#")){
        let target_= window.location.href.split("#")[1]
        if (target_!=="Home"){
          this.target_page=target_
        }
      }else{
        
      }
     
    },
