    },
    track_page(page_name){
      try{
        //logEvent(this.analytics_, 'page_visited', {page_name:page_name})
        //change also url for easen GA reports
        let new_url=window.location.href.slice(0,window.location.href.lastIndexOf("/"))+"#"+page_name
        new_url.replace("/#",'#')
        //console.log(window.location.href, new_url)
        //window.location.href= new_url
        logEvent(this.analytics_, page_name, {page_name:page_name})
        logEvent(this.analytics_,"screen_view",{page_title:page_name})
        logEvent(this.analytics_,"page_view",{page_title:page_name})



        
      }catch (error){
        console.log("ERROR", error)
      }
    },
    track_clicks(e){
      //log any click on page
      //remember to fix path (in case of path the parent element must be returned)
      let parent_innerHTML=`${e.target.parentNode.innerHTML}`
      let node_innerHTML=`${e.target.innerHTML}`
      if (parent_innerHTML.startsWith("<path")){
        //fix these two
        parent_innerHTML=`${e.target.parentNode.parentNode.parentNode.parentNode.parentNode.innerHTML}`
        node_innerHTML=`${e.target.parentNode.parentNode.parentNode.parentNode.innerHTML}`
      }
      logEvent(this.analytics_, "click", {page_title:this.target_page,node_innerHTML:node_innerHTML, parent_innerHTML: parent_innerHTML })

    }
  
  },
