}

.el_counter{
    text-align: center;
    width:35%;
    font-size:25px
}

@keyframes rotate3d {
            0% {
                transform: perspective(1000px) rotateY(360deg);
            }

            100% {
                transform: perspective(1000px) rotateY(0deg);
            }
        }

@keyframes pulse {
            0% {
                transform: scale(1.3);
                
            }

            10% {
                transform: scale(1);

            }

            25%{
                transform: scale(1.27)
            }


            90%{
                transform: scale(1)
            }

            100% {
                transform: scale(1.3);
            }
        }

@keyframes tilt {
            0% {
                transform: rotate(-10deg);
                
            }

            50% {
                transform: rotate(10deg);

            }

            100% {
                transform: rotate(-10deg);
            }
        }

.heart_icon{
    width: 25%;
    flex:1;
    font-size: 30px;
    color:var(--body_bg)
}


.heart_icon_active{
    width: 25%;

    color:var(--bs-danger);
    font-size: 30px;
    animation: pulse 2s ease infinite 
}

.cart_icon{
    flex:1;
     width: 25%;

    font-size: 30px;
    color:rgb(89, 164, 244);

}


.cart_icon{
    width: 25%;

        font-size: 30px;

    color:var(--bs-info);

}
