    }
    .for_shop h3{
        font-size: 25px!important;
        color:var(--bs-danger);
        font-weight: bolder;
        text-shadow: none;
        margin-top: 1%!important;
    }
    .thought_cont{
    display: flex;
    flex-direction: row;
    margin-left: 0%


}
    }

    @media (orientation:landscape){
        .for_shop_img{
        top:0;
        left:0;
        width: 25vw;
        height: 25vh;
        scale: 0.8;
        

    }

    .for_shop{

        margin-bottom:12vh!important;
        height: 54vh!important;


    }
    #product_state_handler_cont, #type_selector_cont, .el_counter{
        margin-left: 35%;
    }
    #type_selector_cont{
        margin-left:25%!important
    }
    }

}

@media (pointer:coarse){

    @media (orientation:portrait){
        .for_shop_img{
        top:0;
